@import url('https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Yanone Kaffeesatz', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
  
  
  background-color: rgb(27, 29, 28);
 
  
}

.App {
  margin-top: 20%;
  
  border: 50px solid rgb(20, 19, 19);
  background-color: rgb(15, 15, 15);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  color: white;
  letter-spacing: 5px;
}

ul, li {
  list-style-type: none; 
}


li:hover {
  transform: scale(1.2);
  transition: .2s;
}
a {
  text-decoration: none;
  display: inline-block;
  margin: 20px;
  font-size: 24px;
  letter-spacing: 4px;
  color: rgb(177, 212, 243);
  
}

.photo {
  width: 400px;
  height: 400px;
  
}

.currency {
  width: 400px;
  height: 400px;
  
}

.quiz {
  width: 400px;
  height: 400px;
  
}
.photo:hover {
  width: 400px;
  height: 300px;
  border: 1px solid transparent;
  border-radius: 5px;
  background-image: url('./img/photo.png');
  -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    color: transparent;
    z-index: 2;
    box-shadow: 8px -8px 5px rgb(9, 92, 16);
}
.currency:hover {
  width: 400px;
  height: 300px;
  
  background-image: url('./img/currency.png');
  border: 1px solid transparent;
  border-radius: 5px;
  -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    color: transparent;
    z-index: 2;
    box-shadow: 8px -8px 5px rgb(43, 52, 175);
}

.quiz:hover {
  width: 400px;
  height: 300px;
  
  background-image: url('./img/quiz.png');
  border: 1px solid transparent;
  border-radius: 5px;
  -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    color: transparent;
    z-index: 2;
    box-shadow: 8px -8px 5px teal;
}